

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main_div {
  background-color: blueviolet;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 15px;
}

.center_div {
  width: 400px;
  height: 400px;
  background-color: #fff; 
  text-align: center;
  border-radius: 15px;

}

h1 {
  background-color: rgb(34, 30, 37);
  height: 3rem;
  line-height: 3rem;
  color: #fff;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: capitalize;
}

input {
  padding: 10px;
  text-transform: capitalize;
  text-align: center;
  font-size: 16px;
  margin-top: 30px;
  outline: none;
}

button {
  margin-left: 20px;
  width: 40px;
  border-radius: 50%;
  border: none;
  height: 40px;
  background-color: blueviolet;
  color: #fff;
  transition: 0.5s;
  align-items: center;
}

button:hover {
  background-color: transparent;
  color: blueviolet;
  /* border: 2px solid blueviolet; */
  border: 4px solid black;
}

button i {
  font-size: 15px;
  font-weight: 500;
}

.item {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  gap: 40px;
}


.todo_style {
  display: flex;
  gap: 20px;
  margin-top: 40px;
  margin-left: 10px;

}
 
.todo_style li {
  text-decoration: none;
}
